* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Inter', sans-serif;
}

#root {
    display: flex;
    margin: 0 auto;
}

.navbar_menu {
    display: flex;
    justify-content: space-between;
    max-width: 650px;
    margin: 0 auto;
    margin-top: 30px;
    margin-bottom: 10px;
}

.navbar_menu a {
    text-decoration: none;
    color: #009FE3;
    font-weight: 400;
    font-size: 24px;
    opacity: 0.5;
}
.navbar_menu_active {
    opacity: 1 !important;
}

.Login_page {
    background-image: url('/public/img/Login_page_background.png');
    width: 100vw;
    height: 100vh;
    position: relative;
    background-repeat: no-repeat;
}

.main_h1 {
    text-align: center;
    color: white;
    font-weight: 400;
    font-size: 31.9736px;
    align-items: center;
    width: 380px;
    margin: 0 auto;
    
}
.position_register_h1 {
    margin-top: 50px;
}
.position_login_h1 {
    margin-top: 200px;
    text-align: center;
}
.input_block {
    text-align: center;
    max-width: 420px;
    margin: 0 auto;
}

.navbar_line {
    background-color:  #54463E;
    height: 4px;
    max-width: 800px;
    margin: 0 auto;
    border-radius: 20px;
    position: relative;
}

.button_login {
    text-align: center;
    margin-top: 20px;
    
}

.button_style {
    width: 398.53px;
    height: 80.72px;
    background: #009FE3;
    border: 2px solid #009FE3;
    border-radius: 50px;
    font-weight: 400;
    font-size: 33.3348px;
    color: white;
    cursor: pointer;
    filter: drop-shadow(4px 4px 6px rgba(0, 0, 0, 0.96));
    align-items: center;
    display: flex;
    margin: 0 auto;
    justify-content: center;
}
.button_style p {
    text-align: center;
}
.button_style svg {
    margin-left: 10px;
}
.input_style {
    width: 398.53px;
    height: 78.84px;
    background: #202327;
    border: 2px solid #14162A;
    border-radius: 50px;
    filter: drop-shadow(4px 4px 6px rgba(0, 0, 0, 0.96));
    margin-top: 20px;
    color: white;
    outline: none;
    padding-left: 20px;
    font-weight: 400;
    font-size: 24px;
}

.line_input {
    border: 1px solid #FFFFFF;
    max-width: 390px;
    margin: 0 auto;
    border-radius: 20px;
    margin-top: 10px;
}

::placeholder {
    font-weight: 400;
    font-size: 24px;
    padding-top: 10px;
}
.Register_page {
    background-image: url('/public/img/Login_page_background.png');
    width: 100vw;
    height: 100vh;
    position: relative;
    background-repeat: no-repeat;
}
.svg_login {
    margin-left: 15px;
}
.Password_page {
    background-image: url('/public/img/Login_page_background.png');
    width: 100vw;
    height: 100vh;
    position: relative;
    background-repeat: no-repeat;
}
.line_hover {
    opacity: 1;
    border: 2px solid #EFA069;
    width: 33.3%;
    position: absolute;
    border-radius: 20px;
}
.speedometer_main {
    background-color: rgb(235, 68, 68);
    width: 100vw;
    height: 100vh;
}